body {
   margin: 0;
}
header {
    width: 100%;
    height: 70px;
    background-color: lightskyblue;
    box-shadow: 0px 2px 10px 0px rgba(0,0,0,0.54);
}

.search-form {
   text-align: center;
   padding-top: 20px;
}
input {
    height: 25px;
    width: 400px;
    border-radius: 4px;
    border: 1px solid lightslategray;
    padding: 5px;
    font-size: 14px;
    outline: none;
}
button {
    height: 36px;
    font-size: 14px;
    border-radius: 4px;
    border: 1px solid lightslategray;
    cursor: pointer;
    margin-left: -5;
}
.load-more {
   margin-top: 30px;
   background-color: lightskyblue;
   color: white;
}
.container {
    text-align: center;
}
button:hover {
    background-color: palegoldenrod;
}
.is-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.body-container {
 width: 1400px;
 margin: 0 auto; 
 margin-top: 50px;
 display: table;
}
.photo-card {  
  flex-basis: (100% - 60px) / 4; 
}
.gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}
.info {
    display: flex;
    gap: 15px;
}
span {
    display: block;
    font-weight: 400;
}